// api.js

//  API
const PREFIX = "/api";

//     
const req = (url, options = {}) => {
  //   ,   
  const { body } = options;

  //     cookie
  let token = null;
  try {
    const tokenCookie = document.cookie.split('; ').find(row => row.startsWith('token='));
    token = tokenCookie ? tokenCookie.split('=')[1] : null;
  } catch (error) {
    console.error('Error getting token from cookie:', error);
  }

  //  URL 
  const requestUrl = (PREFIX + url).replace(/\/\/$/, "");

  //  fetch-
  return fetch(requestUrl, {
    //   
    ...options,
    //     JSON,   
    body: body ? JSON.stringify(body) : null,
    //  
    headers: {
      //   
      ...options.headers,
      //  Content-Type  JSON-
      ...(body ? { "Content-Type": "application/json" } : null),
      //  ,   
      Authorization: token ? `Bearer ${token}` : undefined,
    },
  })
  //  
  .then((res) => {
    //   
    if (res.ok) {
      //   DELETE-   204 (No Content),    
      if (options.method === 'DELETE' && res.status === 204) {
        return res;
      }
      //        JSON
      return res.json();
    } else {
      //   ,      
      return res.text().then((message) => {
        throw new Error(message);
      });
    }
  });
};

//     
export const getNotes = ({ age, search, archived } = {}) => {
  //  URL 
  let url = `/api/notes`;
  const params = new URLSearchParams();
  if (age) params.append("age", age);
  if (search) params.append("search", search);
  if (archived) params.append("archived", archived);

  if (Array.from(params).length) {
    url += `?${params.toString()}`;
  }

  //  fetch-
  return fetch(url)
    .then(response => {
      //   ,  
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      //    JSON
      return response.json();
    })
    .then(data => {
      //    
      console.log("Data from getNotes:", data);
      //  
      return data;
    });
};

//     
export const createNote = (title, content) => {
  return req(`/notes`, {
    method: "POST",
    body: { title, content },
  });
};

//      ID
export const getNote = (id) => {
  return req(`/notes/${id}`);
};

//    
export const archiveNote = (id) => {
  return req(`/notes/${id}/archive`, {
    method: "PATCH",
  });
};

//    
export const unarchiveNote = (id) => {
  return req(`/notes/${id}/unarchive`, {
    method: "PATCH",
  });
};

//    
export const editNote = (id, title, content) => {
  return req(`/notes/${id}`, {
    method: "PUT",
    body: { title, content },
  });
};

//    
export const deleteNote = (id) => {
  return req(`/notes/${id}`, {
    method: "DELETE",
  });
};

//      
export const deleteAllArchived = () => {
  return req(`/notes/archived/deleteAll`, {
    method: "DELETE",
  });
};

//    URL PDF- 
export const notePdfUrl = (id) => {
  return `/api/notes/${id}/pdf`;
};

//    PDF- 
export const downloadNotePdf = (id) => {
  //   
  const link = document.createElement('a');
  link.href = notePdfUrl(id);
  link.target = '_blank'; //    
  link.download = `note-${id}.pdf`; //   

  //     (  )
  document.body.appendChild(link);

  //    
  link.click();

  //    
  document.body.removeChild(link);
};
